form{
    display: flex;
    flex-direction: column;
    margin: 100px 40px 40px 40px;
    border-radius: 20px;
    border: 1px solid #E6EFF5;
    width: 880px;
}

.tabs{
    display: flex;
    gap: 20px;
    margin-bottom: 15px;
    &>button {
        background: none;
        border: none;
        margin: 34px 22px;
        cursor: pointer;
        color: #718EBF;
    }
    &>.active {
        color: #007bff;
    }
}

.wrapper{
    display: flex;
    justify-content: center;
    &>img{
        margin-top: 6px;
        margin-left: 24px;
        width: 104px;
        height: 104px;
        border-radius:50%;
    }
}

.form_section{
    display: grid;
    grid-template-columns: repeat(2, 40%);
    column-gap: 72px;
    margin-left: 36px;

    .input_group {
        &>label {
          display: block;
          margin-bottom: 10px;
          font-size: 14px;
        }
        &>input {
          width: 330px;
          padding: 11px 0;
          border: 1px solid #E6EFF5;
          border-radius: 8px;
          margin-bottom: 15px;
          &::placeholder{
            padding-left: 20px;
          }
        }
    }
    &>button{
        margin: 5px 0 25px 190px;
        width: 150px;
        border-radius: 15px;
        border: none;
        color: white;
        height: 41px;
        background-color:#1814F3;
    }
}


@media (max-width:1024px) {
    form{
        margin-left: 5%;
        margin-right: 5%;
        width: 90%;
        margin-top: 130px;
    }
    
    .tabs{
        gap: 0;
        justify-content: center;
        margin-bottom: 0;
        width: max-content;
        &>button{
            font-size: 12px;
            margin-top: 10px;
        }
    }
    .wrapper{
        flex-direction: column;
        align-items:center;
        &>img{
            margin-bottom: 20px;
        }
    }
    .form_section{
        grid-template-columns: none;
        justify-content: stretch;
        margin: 0;
        &>.input_group {
            &>label{
                font-size: 12px;
                margin: 15px 0 5px 0;
            }
            &>input{
                padding: 8px 0;
                width: 250px;
                margin: 1px 0;
            }
        }
        &>div{
            width: 60%;
        }
        &>button{
            margin: 5px 0;
            width: 100%;
        }
    }
}