header{
    height: 80px;
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    border-bottom: 2px solid #E6EFF5;
}

.logo {
    height: 100%;
    display: flex;
    align-items:center;
    img{
        width: 28px;
        margin: 0 7px 0 37px;
    }
    h1{
        font-size: 23px;
        color: #343C6A;
    }
}

.wrapper_content_header{
    margin-left: 55px;
    width: 80%;
    display: flex;
    height: 100%;
    align-items: center;
    color: #343C6A;
    div{
        display: flex;
        height: 100%;
        align-items: center;
        input{
            margin-left: 400px;
            width: 200px;
            border-radius: 25px;
            border: none;
            height: 40px;
            background-color: #F5F7FA;
            &::placeholder{
                padding-left: 27px;
            }
        }
        span{
            margin-left: 31px;
            height: 40px;
            width: 40px;
            border-radius: 50%;
            background-color:#F5F7FA;
            display: flex;
            justify-content: center;
            &>img{
                width: 50%;
            }
        }

        &>img{
            height: 45px;

            width: 45px;

            margin-left: 25px;
            border-radius: 50%;
        }
        
    }
}

@media (max-width: 1024px) {
    header{
        display: flex;
        flex-direction: column;
        height: auto;
        margin-top: 10px;
        &>input{
            margin: 5% 0;
            width: 80%;
            border-radius: 25px;
            border: none;
            height: 30px;
            background-color: #F5F7FA;
            &::placeholder{
                padding-left: 20px;
            }
        }
    }
    
    h2{
        font-size: 18px;
        color: #343C6A;
    }

    .wrapper_content_header_mobile{
        display:grid;
        flex-direction: column;
        justify-content: space-between;
        align-items:center;
        justify-items: center;
        width: 100%;
        grid-template-columns: repeat(3, 30%);
        color: #343C6A;

        &>span{
            width: 16px;
            height: 16px;
            &>img{
            width: 100%;
                height: 100%;
            }
        }
        &>img{
            width: 35px;
            height: 35px;
            border-radius: 50%;
        }
    }
}