aside {
    margin-top: 52px;
    padding: 20px;
    border-right: 2px solid #E6EFF5;
}

nav ul {
    list-style: none;
    padding: 30px 6px 0px 12px;
    &>li {
        display: flex;
        align-items: center;
        margin-bottom: 29.5px;
        cursor: pointer;
        color: #B1B1B1;
        font-size: 17px;
        width: max-content;
        &>img{
            width: 22px;
            padding-right: 24px;
        }

        &.active {
            color: #007bff;
            font-weight: bold;
        }
    }
}